@import url('https://fonts.googleapis.com/css2?family=Cookie&display=swap');


.navbar{
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    flex-wrap: nowrap;
    background-color: #2779a7;
    width: 100%;
    height: 70px;
    align-items: center;
    justify-content: flex-start;
    box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.5);
}
.navbar--href:not(.navbar--logo){
    text-decoration: none;
    color: #abe3ff;
    font-weight: bolder;
    font-size: 18px;
}
.navbar > *:not(.navbar--logo){
    margin-right: 15px;
    opacity: 0.75;
        text-shadow: -0.5px -0.5px #3980a4,
                0.5px -0.5px #3980a4,
                -0.5px 0.5px #3980a4,
                0.5px 0.5px #3980a4;
    transition: all 250ms;
}
.navbar--href:not(.nav-bar--logo):hover {
    opacity: 1;
}

.navbar--logo{
    text-shadow: -2px -2px #013b59,
            2px -2px #013b59,
            -2px 2px #013b59,
            2px 2px #013b59;
    margin-left: 7.5px;
    margin-right: auto;
    font-size: 40px;
    color: whitesmoke;
    font-family: 'Cookie', cursive;
}
.navbar--active{
    text-decoration: underline;
}