.inp_details{
    background-color: #2779a7;
    padding: 35px 35px 35px 35px;
    margin-top: 20%;
    display: -ms-grid;
    display: -moz-grid;
    display: grid;
    justify-items: center;
    border-radius: 30px 30px 30px 30px;
    box-shadow: 2.5px 1.25px 10px black, -2.5px -1.25px 10px black;
}

.inp_details--div{
    display: -ms-grid;
    display: -moz-grid;
    display: grid;
    justify-items: center;
    text-align: center;
    margin: 50px 0 0 0;
    padding: 5px 10px 10px 10px;
    width: 300px;
    border-radius: 30px 30px 30px 30px;
    outline: 2px solid black;
    box-shadow: 2.5px 1.25px 10px #6db4dd, -2.5px -1.25px 10px #6db4dd;
}

.inp_details--h1{
    margin: 0;
    font-size: 29px;
    font-style: italic;
    color: transparent;
    text-shadow: 21px 10px 0 #fff, -20px -10px 0 #fff, -24.5px -10px 0 #000, 25px 10px 0 #000;
    text-decoration: underline;
    animation: statusAnim 2s ease-in-out alternate-reverse infinite;
    
}
.inp_details--fa{
    margin: 0;
    font-size: 30px;
}
.fa--warning{
    color: #ff5249;
}
.fa--success{
    color: #5ad851;
}
.fa--awaiting{
    opacity: 0.7;
    color: gainsboro;
}

.inp_details--p{
    font-weight: 600;
    font-size: 17px;
    margin: 2px 0 0 0;
}

@keyframes statusAnim{
    0%{
        text-shadow: 21px 10px 0 #fff, -20px -10px 0 #fff, -24.5px -10px 0 #000, 25px 10px 0 #000;
    }
    100%{
        text-shadow: 60px 10px 0 #fff, -60px -10px 0 #fff, -54px -10px 0 #000, 54px 10px 0 #000;
    }   
}