.login {
    display: -ms-grid;
    display: -moz-grid;
    display: grid;
    justify-content: center;
    text-align: center;
}

.login--div{
    background-color: #d8eefc;
    padding: 35px 0 35px 0;
    margin-top: 20%;
    display: -ms-grid;
    display: -moz-grid;
    display: grid;
    justify-items: center;
    border-radius: 30px 30px 30px 30px;
    box-shadow: 2.5px 1.25px 10px black, -2.5px -1.25px 10px black;
}
.login--div--button {
    cursor: pointer;
    width: 100px;
    margin-top: 20px;
    margin-right: 20px;
    margin-left: 20px;
    font-size: 16px;
    font-weight: bold;
    color: black;
    background: #d8eefc;
    border: 2px solid black;
    box-shadow: 10px 5px 0 black, -10px -5px 0 black;
    transition: all 250ms;
}
.login--div--h1{
    margin-top: -10px;
    text-decoration: underline;
    font-size: 25px;
}
.login--div--button:hover {
    box-shadow: 5px -5px 0 black, -5px 5px 0 black;
}
.input--form{
    padding-left: 8px;
    border-radius: 20px 20px 20px 20px;
    height: 28px;
    width: 17em;
    box-shadow: 10px 5px 10px rgba(0, 0, 0, 0.2), -5px -5px 5px rgba(0, 0, 0, 0.1);
}
.login--div--p{
    margin-bottom: 0px;
    margin-right: auto;
    margin-left: 80px;
    font-weight: 500;
}