.settings{
    display: -moz-grid;
    display: -ms-grid;
    display: grid;
}
.settings--div--top{
    margin-left: 10px;
    margin-top: 40px;
    display: -moz-grid;
    display: -ms-grid;
    display: grid;
    align-items: center;
    text-align: justify;
    grid-template-columns: 1fr 2fr;
}
.div--subdiv--details{
    display: grid;
    align-items: center;
}
.settings--div--button {
    cursor: pointer;
    width: 200px;
    margin-top: 20px;
    margin-right: 20px;
    margin-left: 20px;
    font-size: 16px;
    font-weight: bold;
    color: black;
    background: #d8eefc;
    border: 2px solid black;
    box-shadow: 10px 5px 0 black, -10px -5px 0 black;
    transition: all 250ms;
}
.settings--div--button:hover {
    box-shadow: 5px -5px 0 black, -5px 5px 0 black;
}
.settings--div--img{
    border-radius: 100% 100% 100% 100%;
    width: 96px;
    height: 96px;
    outline: 3px solid black;
    transition: opacity 100ms;
}
.settings--div--img:hover{
    opacity: 0.5;
}
.settings--dispname{
    margin: 0;
    font-weight: 600;
    font-size: 20px;
}
.settings--email{
    font-size: 15px;
    font-weight: 500;
    margin: 5px 0 0 8px;
}
.line{
    margin: 20px 0 20px 0;
    width: 100vw;
    height: 2px;
    background-color: black;
}
.settings-p{
    font-style: italic;
    display: inline-block;
    padding: 8px;
    box-shadow: 5px 5px whitesmoke, -5px -5px whitesmoke;
    font-weight: 600;
    font-size: 18px;
    margin: 15px 0 0 20px;
}
.input--form {
    padding-left: 8px;
    border-radius: 20px 20px 20px 20px;
    height: 28px;
    width: 17em;
    margin-left: 20px;
    margin-bottom: 5px;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.2), -5px -5px 5px rgba(0, 0, 0, 0.1);
}
.fa--save{
    font-size: 35px;
    color: whitesmoke;
    box-shadow: 5px 5px 0 black, -5px -5px 0 black;
    margin-left: 10px;
    transform: translateY(38%);
}
.fa--edit{
    font-size: 20px;
    transform: translateX(-100%) translateY(25%);
    margin: 0;
}
.btn--pass{
    margin-left: 35px;
    margin-bottom: 5px;
    width: 150px;
}
.btn--log{
    width: 300px;
    justify-self: center;
    height: 35px;
    letter-spacing: 2px;
    font-style: italic;
}
.first{
    margin-top: 40px;
}
