@import url('https://fonts.googleapis.com/css2?family=Work+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

h3{
    margin: 0;
}

.home{
    display: -moz-grid;
    display: -ms-grid;
    display: grid;
    height: calc(100vh - 70px); /*70px = header height*/
    justify-content: center;
    align-items: flex-start;
    grid-template-rows: max-content;
}
.home--div--account{
    border: 2px solid black;
    border-radius: 3%;
    box-shadow: 10px 10px 5px rgba(0, 0, 0, 0.25);
    padding: 10px 10px 10px 10px;
    margin-top: 35px;
    margin-bottom: 55px;
    background-color: #F4F5FF;
}
.home--div--slogan, .home--div--unlimited{
    display: -moz-grid;
    display: -ms-grid;
    display: grid;
    justify-content: center;
    text-align: center;
    box-shadow: 1px 1px 10px black;
}
.div--h3--slogan, .div--h3--unlimited{
    margin: 0;
    font-size: 20px;
    text-decoration: underline;   
}
.div--fa--slogan{
    font-size: 25px;
}
.div--h1--slogan, .div--h1--unlimited{
    z-index: 1;
    margin: 0;
    font-size: 40px;
    letter-spacing: 6px;
}
.div--fa--world{
    color: #0BDA51;
    z-index: 0;
    margin-top: -50px;
    font-size: 250px;
    animation: earthSpin 15s ease-in-out alternate-reverse infinite;
}
.div--h1--account{
    font-family: 'Work Sans';
    font-weight: 400;
    letter-spacing: -1px;
    margin-top: 0;
    font-size: 30px;
    margin-bottom: 7.5px;
}
.div--h3--account{
    font-size: 20px;
    font-family: 'Work Sans';
    letter-spacing: 1px;
    font-style: italic;
}
.div--button{
    cursor: pointer;
    width: 100px;
    margin-top: 20px;
    margin-right: 20px;
    margin-left: 20px;
    font-size: 16px;
    font-weight: bold;
    color: black;
    background: #d8eefc;
    border: 2px solid black;
    box-shadow: 10px 5px 0 black, -10px -5px 0 black;
    transition: all 250ms;
}
.div--button:hover{
    box-shadow: 5px -5px 0 black, -5px 5px 0 black;
}
.div--fa--infinite{
    z-index: 0;
    font-size: 200px;
    margin-top: -75px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: -30px;
    color :whitesmoke;
}

/*Animations*/
@keyframes earthSpin {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(359deg);
    }
}
